.paginationContainer {
  width: 312px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.paginationButtonsBlock {
  color: var(--text-prime);
  display: flex;
  flex-direction: row;
  gap: 8px;

  text-align: center;
  font-family: Poppins, sans-serif;
  font-size: 14px;

  font-weight: 500;
  line-height: 18px;

  margin-top: 40px;
  margin-bottom: 80px;

}

.paginationButtons {
  min-width: 32px;
  height: 32px;
  padding: 10px;

  border-radius: 4px;
  border: 1px solid rgba(250, 250, 250, 0.3);
  fill: var(--text-prime);
  background: #171717;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.buttonsActive {
  border-color: var(--blue);
}
.paginationButtons[disabled],
.paginationButtons[disabled] > .navigationIcon {
  border-color: transparent;
  color: var(--text-secondary);
  fill: var(--text-secondary);
}

.paginationButtons:hover {
  color: var(--text-prime);
  background-color: var(--blue);
  border-color: var(--blue);
}

.navigationIcon {
  fill: var(--text-prime);
  width: 24px;
  height: 24px;
}

.paginationButtons:has(.navigationIcon) {
  width: 32px;
  padding: 0;
}
@media screen and (min-width: 768px) {
  .paginationButtonsBlock {
    margin-top: 64px;
  }
}