:root {
  --baseSize: 427px;
}

.card {
  width: calc(100vw / 2 + 3px);
  position: relative;
  overflow: hidden;
  border-radius: var(--radius);
  z-index: 1;
  align-self: flex-start;
  aspect-ratio: 95/128;
}

.charImage {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top right;
  width: 100%;
  height: 100%;
  z-index: 3;
  filter: drop-shadow(2px 2px 4px #292828);
}

.charImage.blue {

  background-image: url('../../images/Hero/LeftCard/panther-dsk.png');
}
.charImage.red {
  background-image: url('../../images/Hero/LeftCard/spider-dsk.png');
  background-position: bottom 24px right;
  background-size: contain;
}
.charImage.green {
  background-image: url('../../images/Hero/LeftCard/hulk-dsk.png');
}

.card.blue {
  background-color: var(--blue);
}
.card.red {
  background-color: var(--red);
}

.card.green {
  background-color: var(--green);
}

.circle:nth-of-type(1),
.circle:nth-of-type(2),
.circle:nth-of-type(3),
.circle:nth-of-type(4),
.circle:nth-of-type(5) {
  position: absolute;
  top: 12px;
  left: 82px;
  display: block;
  border: 1px solid #ffffff99;
  box-shadow: 0px 0px 1px 0px #ffffff99;
  width: var(--baseSize);
  height: var(--baseSize);
  padding: 9px;
  border-radius: 50%;
  z-index: 2;
}
.circle:nth-of-type(1):after,
.circle:nth-of-type(2):after,
.circle:nth-of-type(3):after,
.circle:nth-of-type(4):after {
  content: '';
  border-radius: 50%;
  display: block;
  position: relative;
  border: 1px solid #ffffff99;
  box-shadow: 0px 0px 1px 0px #ffffff99;
  width: calc(var(--baseSize) - 18px);
  height: calc(var(--baseSize) - 18px);
  z-index: 2;
}

.circle:nth-of-type(2) {
  width: calc(var(--baseSize) - 76px);
  height: calc(var(--baseSize) - 76px);
  top: 50px;
  left: 120px;
  padding: 6px;
}
.circle:nth-of-type(2):after {
  width: calc(var(--baseSize) - 76px - 12px);
  height: calc(var(--baseSize) - 76px - 12px);
}
.circle:nth-of-type(3) {
  width: calc(var(--baseSize) - 120px);
  height: calc(var(--baseSize) - 120px);
  top: 72px;
  left: 142px;
  padding: 6px;
}
.circle:nth-of-type(3):after {
  width: calc(var(--baseSize) - 120px - 12px);
  height: calc(var(--baseSize) - 120px - 12px);
}
.circle:nth-of-type(4) {
  width: calc(var(--baseSize) - 220px);
  height: calc(var(--baseSize) - 220px);
  top: 122px;
  left: 192px;
  padding: 6px;
}
.circle:nth-of-type(4):after {
  width: calc(var(--baseSize) - 220px - 12px);
  height: calc(var(--baseSize) - 220px - 12px);
}
.circle:nth-of-type(5) {
  width: calc(var(--baseSize) - 258px);
  height: calc(var(--baseSize) - 258px);
  top: 141px;
  left: 211px;
}



/* @media screen and (min-width: 768px){


} */

@media screen and (min-width: 1440px){
  .card {
    width: 352px;
    height: 540px;

  
    z-index: 1;
  }
  .charImage {
    position: relative;
    background-size:auto;
    background-repeat: no-repeat;
    background-position: bottom right;
    width: 100%;
    height: 100%;
    z-index: 3;
    filter: drop-shadow(2px 2px 4px #292828);
  }

}